.Music {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  
    position: relative;
    min-height: 100vh;
    
    color: whitesmoke;
}

.Music h2 {
    font-size: 300%;
    padding: 1vh 0;
}

.MusicPlatformsLinks {
    display: flex;
    justify-content: center;
    margin: 3vh 0;
}

.MusicPlatformButton {
    display: flex;
    font-size: 250%;
    margin: 1vh 1vw;
    width: 25vw;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.MusicPlatformButton i {
    color: whitesmoke;
    margin: 0 1vh;
    transition: all 0.2s ease-out;
}

.MusicPlatformButton i:hover {
    color: #e871af;
    transition: all 0.2s ease-out;
}


@media screen and (max-width: 1024px) {
    .MusicPlatformButton {
        display: flex;
        font-size: 200%;
        margin: 1vh 1vw;
        width: 80vw;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
}