.Release {
    color: whitesmoke;
    text-align: center;
}

.releaseTitle {
    padding-top: 1rem;
    font-family: 'GMarketSansMedium';
    font-style: italic;
}

.albumDesc {
    text-align: left;
}

.streamButton {
    width: 100%;
    font-size: 150%;
}

.tracklist {
    font-size: 120%;
    text-align: left;
}

.tracklist .row {
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 767px) {
    .tracklist {
        font-size: 100%;
    }

    .tracklist .row {
        justify-content: center;
        padding-bottom: 1%;
    }
}