.App {
  position: relative;
  min-height: 100vh;
  padding: 9vh 0 12vh 0;

  font-family: 'Comfortaa', sans-serif;

  text-shadow: grey 1px 1px;
}

.backgroundImage {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

@font-face {
  font-family: 'GmarketSansMedium';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

.Title {
  font-family: 'GMarketSansMedium';
  font-size: 4.5rem;
  font-weight: 600;
  text-align: center;
  font-style: italic;
  letter-spacing: 0.5rem;
  text-shadow: #e871af 4px 4px;
  margin: 2vh 0;
}