.Art {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  
    position: relative;
    min-height: 100vh;
    padding: 0 8vw;
    
    color: whitesmoke;
}

.Art p {
    font-size: 1.5rem;   
}

#commissionButton {
    font-size: 200%;
    width: 100%;
}

#galleryButton {
    font-size: 200%;
    margin-bottom: 1vh;
    width: 100%;
}

#ref {
    width: 75%;
    height: 75%;
}

@media screen and (max-width: 1024px) {
    #ref {
        width: 100%;
        height: 100%;
    }
}