.HomeCards {
  display: flex;
  flex-direction: column;
  padding: 7vh 7vw;
  color: #202020;
  text-align: center;
}

.placeholderIcons {
  font-size: 400%;
  margin-top: 1rem;
}

.card {
  margin: 2vh 1vw;
  transition: all 0.2s linear;
}
.card:hover {
  transition: all 0.2s linear;
}
#musicCard:hover {
  background-color: rgb(104, 255, 255);
}
#artCard:hover {
  background-color: #feb96b;
}
#downtimeCard:hover {
  background-color: chartreuse;
}
#aboutCard:hover {
  background-color: #e871af;
}

.card-title {
  font-size: 2rem;
}
.card-text {
  font-size: 1.25rem;
}

a {
  color: #202020;
  text-decoration: none;
}

a:hover {
  color: #202020;
  text-decoration: none;
}
