.NavbarBar {
  padding: 0;
  width: 100%;
  height: 9vh;

  background-color: #202020;

  text-shadow: none;
}

.NavbarTitle {
  font-family: 'GMarketSansMedium';
  font-size: 200%;
  font-style: italic;
  font-weight: 600;

  padding-top: 1vh;
  padding-left: 5vw;

  text-shadow: #e871af 4px 4px;
  letter-spacing: 0.5rem;

  transition: all 0.1s ease-out;
}

.NavbarTitle:hover {
  color: #e871af;

  text-shadow: whitesmoke 4px 4px;
  
  transition: all 0.1s ease-out;
}

.NavbarMenu {
  display: grid;
  grid-template-columns: repeat(7, auto);
  grid-gap: 2vw;
  text-align: center;
  padding-right: 5vw;
}

.NavbarLinks {
  display: flex;
  align-items: center;
  text-align: center;
  line-height: 115%;

  height: 6vh;
  margin: 0.5vh 0 0.5vh 0;

  font-size: 2.5vh;
  font-weight: 600;
  font-family: 'GMarketSansMedium';
  color: whitesmoke;

  transition: all 0.25s ease-out;
}

.NavbarLinks:hover,
.NavbarLinks.active {
  border-bottom: 0.5vh solid #e871af;
  color: #e871af;
  transition: all 0.25s ease-out;
}

.MenuIcon {
  display: none;
}

.MobileMenu {
  background-color: lightgrey;
}

.MobileMenuBody {
  padding-top: 0;
}




@media screen and (max-width: 1024px) {
  .NavbarMenu,
  .NavbarLinks {
    display: none;
  }

  .NavbarTitle {
    letter-spacing: 0.1rem;
  }

  .MenuIcon {
    display: flex;
    align-items: flex-start;
    color: whitesmoke;
    padding-right: 5vh;
  }

  .MobileMenuLinks {
    font-size: 125%;
    font-weight: 400;
  }

}