.About {
    color: whitesmoke;
    
}

.biography {
    font-size: 125%;
    justify-content: center;
}

.SocialMedia {
    text-align: center;
    font-size: 250%;
}

.SocialMedia i {
    color: whitesmoke;
    margin: 0 1vw;
    transition: all 0.2s ease-out;
}
.SocialMedia i:hover {
    color: #e871af;
    transition: all 0.2s ease-out;
}

.characterList {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and (max-width: 767px) {
    .SocialMedia i {
        margin: 0 6vw;
    }
}