.ContentRatings {
    margin: 0 10vw;
    color: whitesmoke;
}

#introParagraph {
    font-size: 120%;
}

.ratingLogo {
    align-items: center;
    width: 172px;
    height: auto;
}

.ratingSummary {
    font-size: 1.25rem;
}

.guidelinesList {
    font-size: 1rem;
}

#contentRatingGuidelinesButton {
    font-size: 2rem;
    margin: 2vh;

    display: flex;
    justify-content: center;
}

@media screen and (max-width: 1360px) {
    .ratingLogo {
        width: 124px;
        height: auto;
    }
}


@media screen and (max-width: 991px) {
    .ratingSummary {
        font-size: 1rem;
    }

    .ContentRatings h3 {
        margin-top: 1vh;
    }
    
    .ratingLogo {
        width: 100px;
        height: auto;
    }
}

@media screen and (max-width: 575px) {
    .ratingLogo {
        width: 75px;
        height: auto;
    }
}