.ArtItem .figure-img {
    object-fit: cover;
    width: 17vw;
    height: 17vw;
}

.artModal {
    display: flex;
    justify-content: center;
}

.artModal .modal-content {
    width: unset;
}

@media screen and (max-width: 1024px) {
    .ArtItem .figure-img {
        width: 35vw;
        height: 35vw;
    }
}

@media screen and (max-width: 767px) {
    .ArtItem .figure-img {
        width: 40vw;
        height: 40vw;
    }
}