.DowntimeWithJesse {
    color: whitesmoke;
}

.Platforms {
    text-align: center;
    font-size: 200%;
}

.Platforms .btn {
    margin: 2vw;
    font-size: 100%;
}

#ytBtn {
    background-color: #cc0000;
    border-color: #cc0000;
}
#ytBtn:hover {
    background-color: whitesmoke;
    color: #cc0000;
}
#twBtn {
    background-color: #9146ff;
    border-color: #9146ff;
}
#twBtn:hover {
    background-color: whitesmoke;
    color: #9146ff;
}
#discordBtn {
    background-color: #5865f2;
    border-color: #5865f2;
    font-size: 2rem;
    margin-top: 0;
}
#discordBtn:hover {
    background-color: whitesmoke;
    color: #5865f2;
}

#pfp {
    width: 25%;
}

@media screen and (max-width: 1024px) {
    .Title {
        font-size: 3rem;
    }
    .Platforms .btn {
        font-size: 60%;
    }
    #pfp {
        width: 100%;
    }
}