.MyFooter {
  position: absolute;
  background-color: #202020;
  bottom: 0;
  width: 100%;

  display: flex;
  flex-direction: row;
}

.Copyright {
  color: whitesmoke;

  font-size: 1rem;

  padding-top: 1vh;

  transition: all 0.1s ease-out;
}

.SocialMediaLinks {
  display: flex;
  padding-right: 2.5vw;
}

.SnsIcon {
  display: flex;

  font-size: 125%;
  color: whitesmoke;

  padding: 0.5vh 0.5vw;
  justify-content: center;

  transition: all 0.1s ease-out;
}

.SnsIcon:hover {
  color: #e871af;

  transition: all 0.1s ease-out;
}

@media screen and (max-width: 1024px) {
  .MyFooter {
    position: absolute;
    bottom: 0;
    width: 100vw;

    flex-direction: column;
  }

  .SocialMediaLinks {
    display: grid;
    grid-template-columns: repeat(5, auto);
    padding-right: 0;
  }

}