.Album {
    text-align: center;
    margin: 1vw;
    width: 100%;
    height: 100%;
}

.mobileAlbumCard {
    display: none;
}

.albumCard {
    margin: 0vw;
    width: 100%;
    justify-content: center;
}

.albumCard:hover {
    background-color: #e871af;
    cursor: pointer;
    transition: all 0.2s linear;
}

@media screen and (max-width: 1024px) {
    .albumCard {
        display: none;
    }

    .mobileAlbumCard {
        display: flex;
        text-align: center;
        margin: 1vw;
    }
    .mobileAlbumCard .card-body {
        padding: 1vh 0;
    }

    .mobileAlbumCard .card-title {
        font-size: 125%;
        text-align: start;
    }
    .mobileAlbumCard .card-text {
        font-size: 100%;
        text-align: start;
    }
}